html,body,ul,li,ol,dl,dd,dt,p,h1,h2,h3,h4,h5,h6,form,fieldset,legend,img {
  margin: 0;
  padding: 0
}
ul,ol {
  list-style: none
}
a{
  text-decoration: none;
}
html,body{
  font-size: .14rem;
  width:100%;
  height: 100%;
}
html{
  font-size: 26.67vw;
}
#app{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}
#content{
  overflow: scroll;
}




